html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.bd__button {
  text-align: right;
  padding: 10px 0;
}

.bd__button > a {
  color: #bdc3cb;
  font-family: sans-serif;
  font-weight: 100;
  text-decoration: none;
}

.bd__button > a:hover {
  color: #798897;
}

.react-clock {
  display: block;
  position: relative;
}

.react-clock, .react-clock *, .react-clock :before, .react-clock :after {
  box-sizing: border-box;
}

.react-clock__face {
  border: 1px solid #000;
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.react-clock__hand {
  position: absolute;
  inset: 0 50%;
}

.react-clock__hand__body {
  background-color: #000;
  position: absolute;
  transform: translateX(-50%);
}

.react-clock__mark {
  position: absolute;
  inset: 0 50%;
}

.react-clock__mark__body {
  background-color: #000;
  position: absolute;
  transform: translateX(-50%);
}

.react-clock__mark__number {
  width: 80px;
  text-align: center;
  position: absolute;
  left: -40px;
}

.react-clock__second-hand__body {
  background-color: red;
}

.react-time-picker {
  display: inline-flex;
  position: relative;
}

.react-time-picker, .react-time-picker *, .react-time-picker :before, .react-time-picker :after {
  box-sizing: border-box;
}

.react-time-picker--disabled {
  color: #6d6d6d;
  background-color: #f0f0f0;
}

.react-time-picker__wrapper {
  border: thin solid gray;
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
}

.react-time-picker__inputGroup {
  min-width: calc(12px + 3.674em);
  box-sizing: content-box;
  flex-grow: 1;
  padding: 0 2px;
}

.react-time-picker__inputGroup__divider {
  white-space: pre;
  padding: 1px 0;
}

.react-time-picker__inputGroup__input {
  min-width: .54em;
  height: 100%;
  font: inherit;
  box-sizing: content-box;
  -moz-appearance: textfield;
  background: none;
  border: 0;
  padding: 0 1px;
  position: relative;
}

.react-time-picker__inputGroup__input::-webkit-outer-spin-button, .react-time-picker__inputGroup__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.react-time-picker__inputGroup__input:invalid {
  background: #ff00001a;
}

.react-time-picker__inputGroup__input--hasLeadingZero {
  margin-left: -.54em;
  padding-left: calc(1px + .54em);
}

.react-time-picker__inputGroup__amPm {
  font: inherit;
  -moz-appearance: menulist;
}

.react-time-picker__button {
  background: none;
  border: 0;
  padding: 4px 6px;
}

.react-time-picker__button:enabled {
  cursor: pointer;
}

.react-time-picker__button:enabled:hover .react-time-picker__button__icon, .react-time-picker__button:enabled:focus .react-time-picker__button__icon {
  stroke: #0078d7;
}

.react-time-picker__button:disabled .react-time-picker__button__icon {
  stroke: #6d6d6d;
}

.react-time-picker__button svg {
  display: inherit;
}

.react-time-picker__clock {
  width: 200px;
  height: 200px;
  max-width: 100vw;
  z-index: 1;
  background-color: #fff;
  border: thin solid #a0a096;
  padding: 25px;
  position: absolute;
  top: 100%;
  left: 0;
}

.react-time-picker__clock--closed {
  display: none;
}

